.app-wrapper {
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
    grid-gap: 0px;
    grid-template-areas:
          'h h'
  'n c';
    width: 1200px;
    margin: 0 auto;
}

.app-wrapper-content {
    background-color: #379683 ;
    min-height: 100vh;
}
